import React from 'react'
import FileDrop from 'react-file-drop'
import '../../styles/popup.css'
import requester from '../../helpers/requester'
import observer from '../../helpers/observer'
import instructions from '../../resources/instructions.png'
import {Redirect} from 'react-router-dom'

export default class Instructions extends React.Component {
    constructor(props){
        super(props);
    }
    
    render(){
        let item = this.props.item;
        let popupStyle = 'hide-popup';
        if(this.props.display)
            popupStyle = 'show-popup';

        return (
            <div className={'modal ' + popupStyle}>

                <div className='modal-content'>
                    <div>
                        <span className='close' onClick={()=>this.props.togglePopup()}>&times;</span>
                    </div>
                    <div className='modal-body'>
                            <FileDrop onDrop={this.handleDrop} className='file-drop'>
                                <img src={instructions}></img>
                                <span>
                                    <h2>Instructions</h2>
                                    <h3>Left click and hold to look around.</h3>
                                    <h3>Right click and hold to move around.</h3>
                                    <h3>Click on each image to zoom and see details.</h3>
                                    <h3>Click on slideshow for an automatic walk around the room.</h3>
                                </span>    
                            </FileDrop>
                    </div>
                </div>

            </div>
        )
    }
}