import React from 'react'
import ReactDOM from 'react-dom'
import withLogging from '../hocs/withLogging'
import Canvas from '../classes/SplashScreen'
import hologram from '../resources/hologramText.png'
import '../styles/text.css'
import observer from '../helpers/observer'

class Gallery extends React.Component {
    
    constructor(params) {
        super(params);
        this.canvas = new Canvas();
        this.state = {interval: '', text: '', index: 0, typing: ''};
        this.toggle = this.toggle.bind(this);
        this.darken = this.darken.bind(this);

        this.names = ['exposition', 'gallery', 'virtual museum', 'presentation'];
        this.updateDimensions = this.updateDimensions.bind(this);
        this.scrollDown = this.scrollDown.bind(this);
    }

    componentDidMount() {
        this.canvas.init(this.refs.game);
        //Toggle the images after 10 seconds
        let autoShow = setInterval(()=>{
            this.toggle();
        },10000);
        setTimeout(()=>{
            this.setState({interval: autoShow});
        },1000);
        window.addEventListener('scroll', this.darken);
        window.addEventListener("resize", this.updateDimensions);
        //Typing effect
        let typing = setInterval(() => {
            let ind = this.state.index;
            if(ind > 3) ind = 0;
            let typing = '';
            this.setState({text: typing});

            for(let i=0;i<this.names[ind].length;i++){
                setTimeout(() => {
                    typing += this.names[ind].charAt(i);
                    this.setState({text: typing});
                }, i*100);
            }
           
            this.setState({index: ind+1});
        }, 2000);
        this.setState({typing});
    }

    componentWillUnmount() {
        this.canvas.unmount();
        clearTimeout(this.state.interval);
        clearTimeout(this.state.typing);
        window.removeEventListener('scroll', this.darken);
        window.removeEventListener("resize", this.updateDimensions);
    }

    updateDimensions() {
        this.canvas.changeSize();
    }

    toggle() {
        this.canvas.restarting();
    }

    darken() {
        let scroll = window.pageYOffset;
        this.gallery.style.filter = 'brightness(' + (100-scroll/6) + '%)';
        this.gallery.style.top = (-scroll/4) + 'px';
        
    }

    scrollDown() {
        window.scroll({
            top: (this.gallery.offsetHeight - 100), 
            left: 0, 
            behavior: 'smooth'
          });
    }

    render() {

        return (
            <div className='gallery-container' ref={(ref) => this.gallery = ref}>
                <div className='text-overlay'>
                    <h2>Virtual Show </h2>
                    <h3 className='anim-typewriter'>Create your {this.state.text}<span className='blinking-cursor'>|</span> in minutes</h3>
                </div>
                <canvas ref='game' className='game-container'/>
                <svg width="100px" height="70px" viewBox="0 0  35 55" preserveAspectRatio="xMidYMid meet" className="round right"  onClick={this.toggle}>
                    <path d="M-15.080837406202939,-8.607060380268411l-3.724999999999998,-3.7250000000000014c-0.625,-0.625,-1.875,0.625,-1.25,1.25l4.858098734320116,2.558952468358001l-4.858098734320116,2.441047531641998c-0.625,0.6250000000000044,0.625,1.8750000000000044,1.25,1.2499999999999991l3.724999999999998,-3.7249999999999996Z" id="e1_shape" transform="matrix(6.17344 0 0 6.17344 129.144 79.7338)"/>
                </svg>
                <svg width="100px" height="70px" viewBox="0 0  45 55" preserveAspectRatio="xMidYMid meet" className="round left"  onClick={this.toggle}>
                    <path d="M-15.080837406202939,-8.607060380268411l-3.724999999999998,-3.7250000000000014c-0.625,-0.625,-1.875,0.625,-1.25,1.25l4.858098734320116,2.558952468358001l-4.858098734320116,2.441047531641998c-0.625,0.6250000000000044,0.625,1.8750000000000044,1.25,1.2499999999999991l3.724999999999998,-3.7249999999999996Z" id="e1_shape" transform="matrix(-6.17095 -0.175397 0.175397 -6.17095 -87.2725 -29.3028)"/>   
                </svg>
                
                <div className="arrow bounce" onClick={this.scrollDown}>
                <svg width="40" height="20" viewBox="-2.5 -5 75 100" preserveAspectRatio="none">
                    <path d="M0,0 l35,50 l35,-50" fill="none" stroke="white" strokeLinecap="round" strokeWidth="6" />
                    <path d="M10,40 l25,50 l25,-50" fill="none" stroke="white" strokeLinecap="round" strokeWidth="5" />
                </svg>
                </div>
            </div>
        );

    }
}
export default Gallery;