import React from 'react'
import ReactDOM from 'react-dom'

import Header from '../Header'
import Gallery from '../Gallery'
import ExpoItem from '../Dashboard/ExpoItem.js'
import '../../styles/text.css'
import styles from '../../styles/home.module.css'
import createButton from '../../resources/createButton.png'
import galleryButton from '../../resources/GalleryButton.png'
import uploadButton from '../../resources/uploadButton.png'
import logoButton from '../../resources/logo2.png'
import observer from '../../helpers/observer'
import {Link, Redirect} from 'react-router-dom'
import requester from '../../helpers/requester'
import {withRouter} from 'react-router-dom'

class Home extends React.Component {
    constructor(props){
        super(props);

        this.state = {ids: [], expositions: []};

    }

    componentDidMount(){
        requester.get('/api/expositions/top')
            .then(res => res.json())
            .then(res => {
                if(res.type !== 'error'){
                    
                    console.log(res.expositions);
                    this.setState({
                        expositions: res.expositions
                    });
                }
                else
                    observer.fire('notifications', res);
            });

        const slider = this.horizontalScroll;
        let isDown = false;
        let startX;
        let scrollLeft;

        slider.addEventListener('mousedown', (e) => {
        isDown = true;
        slider.classList.add('active');
        startX = e.pageX - slider.offsetLeft;
        scrollLeft = slider.scrollLeft;
        });
        slider.addEventListener('mouseleave', () => {
        isDown = false;
        slider.classList.remove('active');
        });
        slider.addEventListener('mouseup', () => {
        isDown = false;
        slider.classList.remove('active');
        });
        slider.addEventListener('mousemove', (e) => {
        if(!isDown) return;
        e.preventDefault();
        const x = e.pageX - slider.offsetLeft;
        const walk = (x - startX) * 3; //scroll-fast
        slider.scrollLeft = scrollLeft - walk;
        console.log(walk);
        });

    }

    scroll = (dir) => {
        const slider = this.horizontalScroll;
        slider.scroll({
            top: 0, 
            left: dir, 
            behavior: 'smooth'
        });
    }

    exposition3D = (url) => {
        this.props.history.push('exposition/view/' + url);
    }

    startPublic = () => {
        this.props.history.push('exposition/public/');
    }

    render(){

        return (
            <div>
                <Header transparent={false}></Header>
                <Gallery></Gallery>
                <div className={styles.content} ref='content'>
                    <button to='/exposition/public' className={"getStarted" + ' ' + styles.started} onClick={this.startPublic}>START NOW</button>
                        <h1 className="bigTitle">Best expositions</h1>
                    <div className={styles.bestExpositions}>
                        <svg width="100px" height="70px" viewBox="0 0  35 55" preserveAspectRatio="xMidYMid meet" className="round right" onClick={() => {this.scroll(this.horizontalScroll.scrollLeft + 200)}}>
                        <   path d="M-15.080837406202939,-8.607060380268411l-3.724999999999998,-3.7250000000000014c-0.625,-0.625,-1.875,0.625,-1.25,1.25l4.858098734320116,2.558952468358001l-4.858098734320116,2.441047531641998c-0.625,0.6250000000000044,0.625,1.8750000000000044,1.25,1.2499999999999991l3.724999999999998,-3.7249999999999996Z" id="e1_shape" transform="matrix(6.17344 0 0 6.17344 129.144 79.7338)"/>
                        </svg>
                        <svg width="100px" height="70px" viewBox="0 0  45 55" preserveAspectRatio="xMidYMid meet" className="round left" onClick={() => {this.scroll(this.horizontalScroll.scrollLeft - 200)}}>
                            <path d="M-15.080837406202939,-8.607060380268411l-3.724999999999998,-3.7250000000000014c-0.625,-0.625,-1.875,0.625,-1.25,1.25l4.858098734320116,2.558952468358001l-4.858098734320116,2.441047531641998c-0.625,0.6250000000000044,0.625,1.8750000000000044,1.25,1.2499999999999991l3.724999999999998,-3.7249999999999996Z" id="e1_shape" transform="matrix(-6.17095 -0.175397 0.175397 -6.17095 -87.2725 -29.3028)"/>   
                        </svg>
                        <section className={styles.horizontalScroll}  ref={(ref) => this.horizontalScroll = ref}>
                        {
                            this.state.expositions.map((expo, i) => <ExpoItem src={expo ? 'http://res.cloudinary.com/dvn7y9omc/image/upload/' + expo.mainImage:''} title={expo.title} key={i} style='big' click={()=>{this.exposition3D(expo.id)}}></ExpoItem>)
                        }
                        </section>
                    </div>


                    <div className={styles.item}>
                        <img src={createButton}></img>
                        <h1>Select gallery</h1>
                        <span>Choose the gallery that fits your spesific style and helps enchance your artworks</span>
                    </div>
                    <div className={styles.item}>
                        <img src={galleryButton}></img>
                        <h1>Upload artworks</h1>
                        <span>Select your images and place them on the walls</span>
                    </div>
                    <div className={styles.item}>
                        <img src={uploadButton}></img>
                        <h1>Publish</h1>
                        <span>Publish and popularize your artworks in front of a bigger audience and find potential buyers for your paintings</span>
                    </div>
                    <div className={styles.item}>
                        <img src={logoButton}></img>
                        <h1>What is VShow?</h1>
                        <span>
                            Vshow is an online platform for creating 3D expositions. Share your artworks, 3D models or digital images by placing them in a digital environment.
                        </span>
                    </div>
                    
                </div>
            </div>
        )
    }

}
export default withRouter(Home);