import React from 'react'
import nail from '../resources/nail.png'

const Loading = () => (
    <div className='loader'>
        <img src={nail} style={{
            position: 'absolute',
            top: '50%',
            width: '20px',
            transform: 'translateY(-50%)',
            left: 0,
            right: 0,
            margin: 'auto',
            height: 'auto',
            zIndex: 1
        }}></img>
        <div className='loading-container'>
            <h1>Loading...</h1>
        </div>
    </div>
);

export default Loading;