import React from 'react'
import ReactDOM from 'react-dom'
import observer from '../../helpers/observer'
import requester from '../../helpers/requester'
import styles from '../../styles/exposition.module.css'

import {Redirect} from 'react-router-dom'
import ThreeContainer from '../ThreeJS/ThreeContainer.js'
import Header from '../Header'
import Popup from '../modal/Popup.js'
import withLogging from '../../hocs/withLogging'


import room1 from '../../resources/room1.png'
import room2 from '../../resources/room2.png'
import room3 from '../../resources/room3.png'
import add from '../../resources/add.png'
import copy from '../../resources/copyButton.png'

import {
    FacebookShareButton,
    FacebookIcon,
    PinterestIcon,
    PinterestShareButton,
    TwitterShareButton,
    TwitterIcon,
    EmailShareButton,
    EmailIcon
  } from 'react-share';

class ExpositionsPublic extends React.Component {
    constructor(params){
        super(params);

        this.chosenImage = this.chosenImage.bind(this);
        this.hideUI = this.hideUI.bind(this);
        this.saveExpo = this.saveExpo.bind(this);
        this.deleteImage = this.deleteImage.bind(this);
        this.stopLoading = this.stopLoading.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
        this.onChange = this.onChange.bind(this);
        this.togglePopup = this.togglePopup.bind(this);
        this.newImage = this.newImage.bind(this);
        this.copyToClipboard = this.copyToClipboard.bind(this);
        

        this.state = {images: [], selected: 0, removing: '', details: {}, submited: false, loading: false, display: false,
            dynamicUI: {
                position: 'absolute', 
                display: 'none',
                top: 0, 
                left: 0
            }
        };
    }
    componentDidMount(){

    }
    componentWillUnmount(){
        
    }

    chosenImage(imgId, e) {
        let temp = JSON.parse(JSON.stringify(this.state.dynamicUI));
        temp.display = 'block';
        temp.left = e.clientX - e.target.offsetLeft+100;
        temp.top = e.clientY - e.target.offsetTop-100;

        this.setState({dynamicUI: temp});

        if(imgId)
            this.setState({selected: imgId});
    }

    hideUI() {
        let temp = JSON.parse(JSON.stringify(this.state.dynamicUI));
        temp.display = 'none';
        this.setState({dynamicUI: temp});
    }

    deleteImage() {
        console.log('Here');
        requester.remove('/api/expositions/image/' + this.state.details._id + '/' + this.state.selected);
        this.setState({removing: this.state.selected});
        setTimeout(() => {
            this.setState({removing: ''});
        }, 30);
    }

    saveExpo(frameId, imgSrc, imgId, position, rotation, normal) {
        position = position.toArray();
        rotation = rotation.toArray();
        console.log(this.state.details._id);
        requester.post({frameId, imgSrc, imgId, matrix: {position, rotation, normal}}, '/api/expositions/edit/' + this.state.details._id);
    }

    dragStart(e) {
        console.log(e.target.id);
        e.dataTransfer.setData("text/id", e.target.id);
    }
    
    stopLoading() {
        this.setState({loading: false});
    }

    onSubmit(event) {
        event.preventDefault();
        this.setState({submited: true, loading: false});
        let id = this.state.details.room;
        setTimeout( () => {
            this.setState({details: {room: id, images: [], title:'temp'}});
        }, 10);

        let details = {room: id, images: [], title:'temp'};
        requester.post(details, '/api/expositions')
                .then(res => res.json())
                .then(res => {
                    if(res.type !== 'error') {
                        let temp = this.state.details;
                        temp._id = res.id;
                        console.log(res.id);
                        this.copy.value = 'http://www.vshow.eu/exposition/view/' + res.id;
                        var event = new Event('input', { bubbles: true });
                        this.copy.dispatchEvent(event);
                        this.setState({details: temp});
                    } else {
                        observer.fire('notifications', {
                            type: res.type,
                            message: res.message
                        });
                    }
                });
    }

    onChange(event) {
        let name = event.target.name;
        let val = event.target.value;

        this.setState({
            details: {room: val}
        });
    }

    togglePopup(event) {
        if(event) event.preventDefault();
        let display = !this.state.display;
        this.setState({display});
    }

    newImage(src) {
        let images = this.state.images;
        images.push(src);
        this.setState({images, display: false});
    }

    copyToClipboard() {
        this.copy.value = 'http://www.vshow.eu/exposition/view/' + this.state.details._id;
        var event = new Event('input', { bubbles: true });
        this.copy.dispatchEvent(event);
        this.copy.select();
        document.execCommand("copy");
        observer.fire('notifications', {
            type: 'info',
            message: 'Url copied to clipboard'
        });
    }

    render(){
        if(this.state.submited)
            return (
                <div onMouseMove={this.chooseImage}>
                    <Header></Header>
                    <Popup display={this.state.display} togglePopup={this.togglePopup} newImage={this.newImage} public={false}></Popup>
                    <ThreeContainer 
                        saveExpo={this.saveExpo} 
                        details={this.state.details}
                        chosenImage={this.chosenImage} 
                        hideUI={this.hideUI} 
                        removing={this.state.removing}
                        edit={true}
                        stopLoading={this.stopLoading}>
                    </ThreeContainer>

                    <span style={this.state.dynamicUI} className='close' onMouseDown={this.deleteImage}>&times;</span>
                    <div className={styles.menu}>
                    <img src={add} className={styles.img} onClick={this.togglePopup}></img>
                    {
                        this.state.images.map((img, i) => 
                        <div className={styles.details} key={i}>
                            <img src={'http://res.cloudinary.com/dvn7y9omc/image/upload/' + img} className={styles.img} draggable='true' onDragStart={this.dragStart}></img>
                            
                        </div>)
                    }
                    </div>
                    <div className={styles.socialContainer}>
                        <img src={copy} className={styles.icon} onClick={this.copyToClipboard}></img>
                        <FacebookShareButton url={'http://vshow.eu/exposition/view/' + this.state.details._id} className={styles.socialButtons}>
                        <FacebookIcon size={40} round={true} />
                        </FacebookShareButton>
                        <TwitterShareButton url={'http://vshow.eu/exposition/view/' + this.state.details._id} className={styles.socialButtons}>
                        <TwitterIcon size={40} round={true} />
                        </TwitterShareButton>
                        <EmailShareButton url={'http://vshow.eu/exposition/view/' + this.state.details._id} className={styles.socialButtons}>
                        <EmailIcon size={40} round={true} />
                        </EmailShareButton>
                    </div>
                    <input type="text" value="Error" ref={(input) => { this.copy = input }}  className={styles.hidden}/>
                </div>
            )
        else 
            return(
                <div>
                    <Header transparent={true}></Header>
                    <div className={styles.content}>
                        <form className={styles.form} onSubmit={this.onSubmit}>
                            <h1 className={styles.textSelect}>Please select room type</h1>
                            <br/><br/><br/>
                            <div className={styles.radioContainer}>
                            <label>
                                <input type='radio' name='room' value={1} onChange={this.onChange} selected/>
                                <img src={room1} alt='could not load'/>
                            </label>

                            <label>
                                <input type='radio' name='room' value={2} onChange={this.onChange} />
                                <img src={room2} alt='could not load'/>
                            </label>

                            <label>
                                <input type='radio' name='room' value={3} onChange={this.onChange} />
                                <img src={room3} alt='could not load'/>
                            </label>
                            </div>
                            <br/><br/><br/><br/><br/><br/>
                            <button type='submit' className='file-button'>Continue</button>
                        </form>
                    </div>
                </div>
                
            )
    }
}

export default withLogging(ExpositionsPublic);